<template>
  <v-form
    ref="form"
    v-model="form.valid"
    lazy-validation
    @keyup.enter.native="onSubmit"
  >
    <h4 class="text-center title__verify">
      We have sent an email with one-time verification code to {{ email }}. Check your email and type the verification code in input field below
    </h4>
    <base-field-input
      label="Verification Code"
      :is-required="true"
      placeholder="Enter Code"
      v-model.trim="form.values.code"
      :rules="form.codeRules"
      :error="Boolean(serverError)"
      :error-messages="serverErrors.code"
      :color="
        form.code.loading ? 'primary' :
        !form.code.invalid ? 'success' :
        form.code.invalid && form.code.checked ? 'error' :
        'primary'"
      validate-on-blur
    >
      <template #append>
        <v-progress-circular
          v-if="form.code.loading"
          class="full-width"
          :size="23"
          :width="3"
          style="width: 20px;"
          color="primary"
          indeterminate
        />

        <v-icon
          v-else-if="form.code.checked"
          :color="form.code.invalid ? 'error' : 'success'"
          size="23"
        >
          {{ form.code.invalid ? 'mdi-information' : 'mdi-check' }}
        </v-icon>
      </template>
    </base-field-input>

    <div
      v-if="resendTimer"
      class="text-center primary--text"
    >
      Temporary password has been sent to your email. <br />
      You can send another request to code in {{ resendTimer }}s
    </div>
    <p
      v-else
      class="text-center primary--text"
    >
      If you didn't get email click the resend button below
    </p>

    <div class="d-flex flex-column-reverse flex-sm-row justify-space-between mt-3">
      <v-btn
        color="primary"
        class="text-uppercase custom__btn mt-5 mt-sm-0"
        depressed
        outlined
        min-width="210"
        large
        :disabled="!!resendTimer"
        :loading="isResendBtnLoading"
        @click="onResend"
      >
        <span class="font-weight-bold">Resend</span>
      </v-btn>
      <v-btn
        color="primary"
        class="text-uppercase custom__btn"
        depressed
        min-width="210"
        large
        :disabled="!form.valid"
        :loading="isLoading"
        @click="onSubmit"
      >
        <span class="font-weight-bold">Next Step</span>
        <v-img
          v-if="!form.code.invalid && form.valid && !isLoading"
          class="account-icon ml-2"
          max-width="18"
          :src="require('@images/icons/next.svg')"
        />
      </v-btn>
    </div>
  </v-form>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import BaseFieldInput from '@components/BaseFieldInput';
import { actions } from '@modules/auth/constants';

import { required } from '@utils/helpers/validationRules';

const { mapActions, mapState } = createNamespacedHelpers('auth');

export default {
  name: 'EmailVerificationForm',
  components: {
    BaseFieldInput,
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data: () => ({
    form: {
      valid: true,
      code: {
        loading: false,
        checked: false,
        invalid: true,
      },
      values: {
        code: '',
      },
      codeRules: [required('Code')],
    },
    serverErrors: {},
    serverError: '',
    resendBtnState: true,
    isResendBtnLoading: false,
    resendTimer: 0,
    interval: null,
    email: '',
  }),
  computed: {
    ...mapState(['isLoading']),
  },
  mounted() {
    this.email = localStorage.getItem('carrier-sign-up-email');
  },
  unmounted() {
    clearInterval(this.interval);
    localStorage.removeItem('carrier-sign-up-email');
  },
  methods: {
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      this.submit(
        { ...this.form.values, email: this.email },
      ).catch((errors) => {
        if (Array.isArray(errors)) {
          this.serverError = errors;
          // eslint-disable-next-line prefer-destructuring
          this.serverErrors.code = errors[0];
        } else if (errors && errors.messages && errors.messages.length) {
          this.serverErrors.code = errors.messages[0].message;
        } else {
          this.serverErrors = errors;
        }
      });
    },
    onResend() {
      this.isResendBtnLoading = true;
      this.resend().then(() => {
        const email = localStorage.getItem('carrier-sign-up-email');
        this.setTimerToResend();
        this.resendBtnState = false;
        this.$notificationSuccess(`Temporary code has been sent to email address: ${email}`);
      }).catch(() => {
        this.resendBtnState = true;
        this.resendTimer = 0;
        this.$notificationError("There was an error on resending email and we couldn't complete action. Please try again");
      }).finally(() => {
        this.isResendBtnLoading = false;
      });
    },
    setTimerToResend() {
      this.resendTimer = 60;
      this.interval = setInterval(() => {
        if (this.resendTimer >= 1) {
          this.resendTimer -= 1;
        }
      }, 1000);
    },
    ...mapActions({
      submit: actions.VERIFY_CARRIER_EMAIL,
      resend: actions.RESEND_EMAIL_TO_CARRIER,
    }),
  },
};
</script>

<style lang="scss" scoped>
  @media(max-width: 599px) {
    .submit {
      position: fixed;
      bottom: 40px;
      right: 20px;
    }
  }

  .title__verify {
    letter-spacing: 0.03em;
    text-align: center;
    color: #174489;
  }
</style>
