<template>
  <v-form
    ref="form"
    v-model="form.valid"
    lazy-validation
    @keyup.enter.native="onSubmit"
  >
    <base-field-file
      label="Certificate of Insurance"
      :is-required="true"
      v-model="form.values.certificate_file"
      placeholder="Upload Certificate"
      :rules="form.certificateRules"
      :error="Boolean(serverErrors && serverErrors.certificate_file)"
      :error-messages="serverErrors.certificate_file"
      @change="onFileChange"
      clearable
    />

    <base-field-datepicker
      label="Expiration Date"
      :is-required="true"
      v-model="form.values.expiration_date"
      :rules="form.exparationDateRules"
      :error="Boolean(serverErrors && serverErrors.expiration_date)"
      :error-messages="serverErrors.expiration_date"
      :allowed-dates="getAllowedDates"
    />

    <base-field-input
      type="string"
      label="Cargo Limit"
      :is-required="true"
      placeholder="Enter Cargo Limit"
      v-model="form.values.cargo_limit"
      prefix="$"
      :rules="form.cargoLimitRules"
      :error="Boolean(serverErrors && serverErrors.cargo_limit)"
      :error-messages="serverErrors.cargo_limit"
    />

    <base-field-input
      type="string"
      label="Deductible"
      :is-required="true"
      v-model="form.values.deductible"
      placeholder="Enter Deductible"
      prefix="$"
      :rules="form.deductibleRules"
      :error="Boolean(serverErrors && serverErrors.deductible)"
      :error-messages="serverErrors.deductible"
    />

    <base-field-input
      label="Agent Name"
      :is-required="true"
      v-model="form.values.agent_name"
      placeholder="Enter Name"
      :rules="form.nameRules"
      :error="Boolean(serverErrors && serverErrors.agent_name)"
      :error-messages="serverErrors.agent_name"
    />

    <base-field-input
      label="Agent phone"
      :is-required="true"
      mask="+1 (###) ###-####"
      v-model.trim="form.values.agent_phone"
      :rules="form.phoneRules"
      :error="Boolean(serverErrors && serverErrors.agent_phone)"
      :error-messages="serverErrors.agent_phone"
    />

    <div class="d-flex flex-column-reverse flex-sm-row justify-space-between mt-3">
      <v-btn
        color="primary"
        class="text-uppercase custom__btn mt-5 mt-sm-0"
        depressed
        outlined
        min-width="215"
        large
        @click="toBack"
      >
        <img
          class="account-icon mr-2"
          :src="require('@images/icons/drivers/back.svg')"
        />
        <span class="font-weight-bold">GO BACK</span>
      </v-btn>
      <v-btn
        color="primary"
        class="text-uppercase custom__btn"
        depressed
        min-width="215"
        large
        :disabled="!form.valid"
        :loading="isLoading"
        @click="onSubmit"
      >
        <span class="font-weight-bold">Next Step</span>
        <img
          class="account-icon ml-2"
          :src="require('@images/icons/next.svg')"
        />
      </v-btn>
    </div>
  </v-form>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { isAfter } from 'date-fns';
import numeral from 'numeral';
import BaseFieldInput from '@components/BaseFieldInput';
import BaseFieldFile from '@components/BaseFieldFile';
import BaseFieldDatepicker from '@components/BaseFieldDatepicker';
import { actions, mutations } from '@modules/auth/constants';

import {
  min,
  decimal,
  required,
} from '@utils/helpers/validationRules';

const { mapActions, mapState, mapMutations } = createNamespacedHelpers('auth');

export default {
  name: 'CargoInsuranceForm',
  components: {
    BaseFieldInput,
    BaseFieldFile,
    BaseFieldDatepicker,
  },
  watch: {
    'form.values.cargo_limit': {
      handler(next) {
        const dotIdx = next.indexOf('.');
        if (dotIdx !== -1 && next.slice(dotIdx + 1).length) {
          const format = `0,0.${'0'.repeat(next.slice(dotIdx + 1).length)}`;
          this.form.values.cargo_limit = numeral(next).format(format);
        } else {
          this.form.values.cargo_limit = numeral(next).format();
        }
      },
    },
    'form.values.deductible': {
      handler(next) {
        const dotIdx = next.indexOf('.');
        if (dotIdx !== -1 && next.slice(dotIdx + 1).length) {
          const format = `0,0.${'0'.repeat(next.slice(dotIdx + 1).length)}`;
          this.form.values.deductible = numeral(next).format(format);
        } else {
          this.form.values.deductible = numeral(next).format();
        }
      },
    },
    'form.values': {
      deep: true,
      handler() {
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data: () => ({
    form: {
      valid: true,
      values: {
        agent_name: '',
        expiration_date: '',
        cargo_limit: '',
        deductible: '',
        agent_phone: '',
        certificate_file: null,
      },
      nameRules: [required('Name')],
      phoneRules: [required('Phone')],
      cargoLimitRules: [
        required('Cargo Limit'),
        (v) => decimal('Cargo Limit')(v.replaceAll(',', '')),
        (v) => min(1000)(+v.replaceAll(',', '')),
      ],
      deductibleRules: [
        required('Deductible'),
        (v) => decimal('Deductible')(v.replaceAll(',', '')),
        (v) => min(1000)(+v.replaceAll(',', '')),
      ],
      certificateRules: [required('Certificate')],
      exparationDateRules: [required('Exparation Date')],
    },
    serverErrors: {},
  }),
  computed: {
    ...mapState(['isLoading']),
  },
  methods: {
    getAllowedDates(date) {
      return isAfter(new Date(date), Date.now());
    },
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      this.form.values.cargo_limit = numeral(this.form.values.cargo_limit).value();
      this.form.values.deductible = numeral(this.form.values.deductible).value();
      this.submit({ ...this.form.values }).catch((errors) => {
        this.serverErrors = errors;
      });
    },
    onFileChange(file) {
      this.form.values.certificate_file = file;
    },
    toBack() {
      this.updateFormStep(2);
    },
    ...mapActions({
      submit: actions.CARGO_INSURANCE_FORM_SUBMIT,
    }),
    ...mapMutations({
      updateFormStep: mutations.UPDATE_CARRIER_SIGN_UP_STEP,
    }),
  },
};
</script>

<style>
  .v-input__append-inner {
    margin-top: 6px !important;
  }
</style>
