<template>
  <div class="d-flex flex-column align-center justify-center pt-sm-11 pb-16">
    <div
      v-if="!$root.isMobile"
      class="content-wrapper d-flex flex-column align-center white box-shadow border-radius mb-5"
    >
      <h1 class="mb-2 font-weight-normal steps">
        Step {{ activeStepNumber }} of {{ stepsCount }}
      </h1>

      <div class="d-flex justify-center align-center">
        <template v-for="{ name, number } in steps">
          <div
            :key="name"
            class="relative"
          >
            <span
              class="step-number d-flex justify-center align-center primary white--text font-weight-bold body-2"
              :class="getStepStatusClasses(number).number"
            >
              {{ number }}
            </span>
            <span
              class="step-name d-flex justify-center primary--text font-weight-bold body-2"
              :class="getStepStatusClasses(number).name"
            >
              {{ name }}
            </span>
          </div>

          <div
            v-if="number !== stepsCount"
            :key="name + number"
            class="mx-3 decor"
            :class="getStepStatusClasses(number).divider"
          ></div>
        </template>
      </div>
    </div>
    <template v-else>
      <div class="align-self-stretch px-5 py-6">
        <div class="d-flex align-center">
          <div class="d-flex justify-center align-center primary mobile-step-number flex-shrink-0 mr-5">
            <span class="mobile-step-number-text white--text font-weight-bold">{{ activeStepNumber }}</span>
          </div>
          <h1>
            {{ steps[activeStepNumber].name }}
          </h1>
        </div>
      </div>

      <v-progress-linear
        class="mb-8"
        :value="activeStepNumber / stepsCount * 100"
      />
    </template>

    <div
      class="step-content-wrapper d-flex flex-column align-center white px-5 py-sm-13 border-radius"
      :class="{ 'box-shadow': !$root.isMobile }"
    >
      <div class="form-wrapper">
        <component :is="steps[activeStepNumber].component" />
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import CarrierInformationForm from '@modules/auth/components/CarrierInformationForm';
import EmailVerificationForm from '@modules/auth/components/EmailVerificationForm';
import CargoInsuranceForm from '@modules/auth/components/CargoInsuranceForm';
import UsdotForm from '@modules/auth/components/UsdotForm';

import { mutations } from '@modules/auth/constants';

const { mapState, mapGetters, mapMutations } = createNamespacedHelpers('auth');

export default {
  name: 'SignUpCarrier',
  components: {
    CarrierInformationForm,
    EmailVerificationForm,
    CargoInsuranceForm,
    UsdotForm,
  },
  computed: {
    ...mapState({
      steps: 'carrierSignUpSteps',
      activeStepNumber: 'carrierSignUpStep',
    }),
    ...mapGetters({
      stepsCount: 'carrierSignUpStepsCount',
    }),
  },
  methods: {
    getStepStatusClasses(number) {
      switch (true) {
        case number === this.activeStepNumber:
          return {
            number: 'primary white--text',
            name: 'primary--text',
            divider: 'primary',
          };
        case number < this.activeStepNumber:
          return {
            number: 'success white--text',
            name: 'success--text',
            divider: 'success',
          };
        default:
          return {
            number: 'inactive-state-background primary--text',
            name: 'primary-grey--text',
            divider: 'inactive-state-background',
          };
      }
    },
    ...mapMutations({
      updateFormStep: mutations.UPDATE_CARRIER_SIGN_UP_STEP,
    }),
  },
  beforeMount() {
    const savedStep = localStorage.getItem('carrier-registarion-step');
    if (savedStep && Number(savedStep) !== Number(this.activeStepNumber)) {
      this.updateFormStep(savedStep);
    }
  },
};
</script>

<style lang="scss" scoped>
  .v-application--wrap {
    color: red;
  }

  .app {
    @media(max-width: 599px) {
      padding-top: 0 !important;

      &::v-deep {
        .v-application--wrap {
          min-height: 0px;
        }
      }
    }
  }
</style>

<style scoped lang="scss">
  .mobile-step-number {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

  .mobile-step-number-text {
    font-size: 24px;
  }

  .content-wrapper {
    max-width: 805px;
    width: 100%;
    padding: 37px 50px 94px;
  }

  .step-content-wrapper {
    max-width: 805px;
    width: 100%;
  }

  .decor {
    height: 2px;
    width: 100px;
  }

  .steps {
    font-size: 40px;
    line-height: 150%;
    /* identical to box height, or 60px */

    text-align: center;
    letter-spacing: 0.03em;

    color: #174489;
  }

  .step-number {
    width: 36px;
    height: 36px;
    border-radius: 50%;
  }

  .step-name {
    position: absolute;
    left: 50%;
    text-align: center;
