<template>
  <v-form
    ref="form"
    v-model="form.valid"
    lazy-validation
    @keyup.enter.native="onSubmit"
  >
    <base-field-input
      label="USDOT"
      :is-required="true"
      placeholder="Enter USDOT"
      v-model.trim="form.values.usdot"
      :rules="form.usdotRules"
      :error="Boolean(serverError)"
      :error-messages="serverErrors.usdot"
      :color="
        form.usdot.loading ? 'primary' :
        !form.usdot.invalid ? 'success' :
        form.usdot.invalid && form.usdot.checked ? 'error' :
        'primary'"
      @input="checkUsdot"
      validate-on-blur
    >
      <template #append>
        <v-progress-circular
          v-if="form.usdot.loading"
          class="full-width"
          :size="23"
          :width="3"
          style="width: 20px;"
          color="primary"
          indeterminate
        />

        <v-icon
          v-else-if="form.usdot.checked"
          :color="form.usdot.invalid ? 'error' : 'success'"
          size="23"
        >
          {{ form.usdot.invalid ? 'mdi-information' : 'mdi-check' }}
        </v-icon>
      </template>
    </base-field-input>

    <base-field-input
      label="Carrier Name"
      :is-required="true"
      :disabled="form.usdot.invalid"
      v-model="form.values.name"
      placeholder="Enter Name"
      :rules="form.nameRules"
    />

    <base-field-input
      label="Address"
      :is-required="true"
      :disabled="form.usdot.invalid"
      v-model="form.values.address"
      placeholder="Enter Address"
      :rules="form.addressRules"
    />

    <base-field-autocomplete
      label="State"
      :is-required="true"
      :disabled="form.usdot.invalid"
      v-model="form.values.state"
      placeholder="Choose state"
      :rules="form.stateRules"
      :items="states"
      item-text="name"
      item-value="id"
      outlined
      dense
    />

    <base-field-input
      label="City"
      :is-required="true"
      :disabled="form.usdot.invalid"
      v-model="form.values.city"
      placeholder="Enter city"
      :rules="form.cityRules"
    />

    <base-field-input
      label="Zip"
      :is-required="true"
      :disabled="form.usdot.invalid"
      v-model="form.values.zip"
      placeholder="Enter zip"
      :rules="form.zipRules"
      validate-on-blur
    />

    <base-field-input
      label="Contact Name"
      :is-required="true"
      :disabled="form.usdot.invalid"
      v-model="form.values.contactName"
      placeholder="Enter contact name"
      :rules="form.contactNameRules"
    />

    <base-field-input
      label="Phone number"
      :is-required="true"
      placeholder="Enter Phone"
      :disabled="form.usdot.invalid"
      v-model="form.values.phone"
      mask="+1 (###) ###-####"
      :rules="form.phoneRules"
      validate-on-blur
    />

    <base-field-input
      label="Fax"
      :disabled="form.usdot.invalid"
      mask="+1 (###) ###-####"
      v-model="form.values.fax"
      placeholder="Enter fax"
    />

    <base-field-input
      label="Email"
      :is-required="true"
      :disabled="form.usdot.invalid"
      autocomplete="email"
      v-model="form.values.email"
      placeholder="Enter email"
      :rules="form.emailRules"
      :error="Boolean(serverErrors && serverErrors.email)"
      :error-messages="serverErrors.email"
    />

    <base-field-input
      label="Password"
      :is-required="true"
      :disabled="form.usdot.invalid"
      placeholder="Enter password"
      v-model.trim="form.values.password"
      :rules="form.passwordRules"
      :type="isPasswordVisible ? 'text' : 'password'"
      autocomplete="new-password"
      :append-icon="isPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
      validate-on-blur
      @click:append="isPasswordVisible = !isPasswordVisible"
    />

    <base-field-input
      label="Confirm Password"
      :is-required="true"
      :disabled="form.usdot.invalid"
      autocomplete="new-password-confirmation"
      placeholder="Enter Confirm Password"
      v-model.trim="form.confirmPassword"
      :rules="[...form.confirmPasswordRules, sameAsPassword]"
      :type="isConfirmPasswordVisible ? 'text' : 'password'"
      :append-icon="isConfirmPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
      validate-on-blur
      @click:append="isConfirmPasswordVisible = !isConfirmPasswordVisible"
    />

    <base-field-textarea
      label="Terms and conditions"
      :disabled="form.usdot.invalid"
      v-model="form.values.termsAndConditions"
      placeholder="Enter terms and conditions"
    />

    <div class="d-flex flex-column-reverse flex-sm-row justify-space-between mt-3">
      <v-btn
        color="primary"
        class="text-uppercase custom__btn w-100 mt-5 mt-sm-0"
        depressed
        outlined
        :to="{ name: this.$constants.ROUTE_NAMES['SIGN_IN']}"
        min-width="215"
        large
        @click="toBack"
      >
        <img
          class="account-icon mr-2"
          :src="require('@images/icons/drivers/back.svg')"
        />
        <span class="font-weight-bold">GO BACK</span>
      </v-btn>
      <v-btn
        color="primary"
        class="text-uppercase custom__btn"
        depressed
        min-width="215"
        large
        :disabled="!form.valid || isLoading || form.usdot.invalid"
        :loading="isLoading"
        @click="onSubmit"
      >
        <span class="font-weight-bold">Next Step</span>
        <v-img
          v-if="!form.usdot.invalid && form.valid && !isLoading"
          class="account-icon ml-2"
          max-width="18"
          :src="require('@images/icons/next.svg')"
        />
      </v-btn>
    </div>
  </v-form>
</template>

<script>
import axios from 'axios';
import debounce from 'debounce';
import { createNamespacedHelpers } from 'vuex';
import BaseFieldInput from '@components/BaseFieldInput';
import BaseFieldTextarea from '@components/BaseFieldTextarea';
import BaseFieldAutocomplete from '@components/BaseFieldAutocomplete';
import { actions } from '@modules/auth/constants';

import {
  required,
  email,
  same,
  zip,
} from '@utils/helpers/validationRules';

const { mapActions, mapState } = createNamespacedHelpers('auth');

export default {
  name: 'CarrierInformationForm',
  components: {
    BaseFieldInput,
    BaseFieldTextarea,
    BaseFieldAutocomplete,
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data: () => ({
    form: {
      valid: false,
      usdot: {
        loading: false,
        checked: false,
        invalid: true,
      },
      values: {
        usdot: '',
        name: '',
        address: '',
        state: null,
        city: '',
        zip: '',
        contactName: '',
        phone: '',
        fax: '',
        email: '',
        password: '',
        termsAndConditions: '',
      },
      confirmPassword: '',
      usdotRules: [required('USDOT')],
      passwordRules: [required('Password')],
      confirmPasswordRules: [required('Confirm Password')],
      nameRules: [required('Carrier Name')],
      addressRules: [required('Address')],
      stateRules: [required('State')],
      cityRules: [required('City')],
      zipRules: [required('Zip'), zip],
      contactNameRules: [required('Contact Name')],
      phoneRules: [required('Phone')],
      emailRules: [required('Email'), email],
    },
    isPasswordVisible: false,
    isConfirmPasswordVisible: false,
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    sameAsPassword() {
      return this.same('Passwords', this.form.values.password);
    },
    ...mapState({
      states: (state) => state.states.data,
      statesLoading: (state) => state.states.loading,
      isLoading: (state) => state.isLoading,
    }),
  },
  methods: {
    same,
    checkUsdot: debounce(
      function bounce() {
        this.form.usdot.loading = true;
        this.form.usdot.checked = false;
        axios.get(`/usdot?usdot=${this.form.values.usdot}`)
          .then((response) => {
            if (this.form.values.usdot) {
              this.form.usdot.invalid = !response.data.valid;
            } else {
              this.form.usdot.invalid = true;
            }
          })
          .catch(() => {
            this.form.usdot.invalid = true;
          })
          .finally(() => {
            this.form.usdot.loading = false;
            this.form.usdot.checked = true;
          });
      },
      250,
    ),
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      localStorage.setItem('carrier-sign-up-email', this.form.values.email);
      this.submit({
        ...this.form.values,
        contact_name: this.form.values.contactName,
        terms_and_conditions: this.form.values.termsAndConditions,
      }).then(() => {
      }).catch((errors) => {
        localStorage.removeItem('carrier-sign-up-email');
        this.serverErrors = errors;
      });
    },
    ...mapActions({
      getStates: actions.GET_STATES,
      submit: actions.CARRIER_INFORMATION_FORM_SUBMIT,
    }),
  },
  beforeMount() {
    this.getStates();
  },
};
</script>

<style>
  .v-input__append-inner {
    margin-top: 6px !important;
  }
</style>
