<template>
  <v-form
    ref="form"
    v-model="form.valid"
    lazy-validation
    @keyup.enter.native="onSubmit"
  >
    <base-field-file
      clearable
      label="USDOT file"
      :is-required="true"
      placeholder="Upload USDOT"
      :rules="form.usdotFileRules"
      :error="Boolean(serverError)"
      @change="onUsdotChange"
    />

    <base-field-file
      clearable
      label="W9 file"
      :is-required="true"
      placeholder="Upload W9"
      :rules="form.w9FileRules"
      :error="Boolean(serverError)"
      :error-messages="serverError"
      @change="onW9Change"
    />

    <div
      class="d-flex flex-column-reverse flex-sm-row justify-space-between mt-3 submit"
    >
      <v-btn
        color="primary"
        class="text-uppercase custom__btn mt-5 mt-sm-0"
        depressed
        outlined
        :block="$vuetify.breakpoint.xsOnly"
        min-width="215"
        large
        @click="toBack"
      >
        <img
          class="account-icon mr-2"
          :src="require('@images/icons/drivers/back.svg')"
        />
        <span class="font-weight-bold">GO BACK</span>
      </v-btn>
      <v-btn
        color="primary"
        class="text-uppercase custom__btn"
        depressed
        min-width="215"
        large
        :disabled="!form.valid"
        :loading="isLoading"
        @click="onSubmit"
      >
        <span class="font-weight-bold">Finish</span>
      </v-btn>
    </div>
  </v-form>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import BaseFieldFile from '@components/BaseFieldFile';
import { actions, mutations } from '@modules/auth/constants';

import { required } from '@utils/helpers/validationRules';

const { mapActions, mapState, mapMutations } = createNamespacedHelpers('auth');

export default {
  name: 'UsdotForm',
  components: {
    BaseFieldFile,
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach((key) => {
          this.serverErrors[key] = '';
        });
      },
    },
  },
  data: () => ({
    form: {
      valid: true,
      values: {
        usdot_file: null,
        w9_file: null,
      },
      usdotFileRules: [required('USDOT File')],
      w9FileRules: [required('W9')],
    },
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    ...mapState(['isLoading']),
  },
  methods: {
    onUsdotChange(file) {
      this.form.values.usdot_file = file;
    },
    onW9Change(file) {
      this.form.values.w9_file = file;
    },
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      this.submit({
        data: { ...this.form.values },
        vm: this,
      }).catch((errors) => {
        if (Array.isArray(errors)) {
          this.serverError = errors;
        } else {
          this.serverErrors = errors;
        }
      });
    },
    toBack() {
      this.updateFormStep(3);
    },
    ...mapActions({
      submit: actions.USDOT_W9_FORM_SUBMIT,
    }),
    ...mapMutations({
      updateFormStep: mutations.UPDATE_CARRIER_SIGN_UP_STEP,
    }),
  },
};
</script>

<style lang="scss" scoped>
@media (max-width: 599px) {
  .submit {
    position: fixed;
    bottom: 40px;
    right: 20px;
    width: 90%;
  }
}
</style>
